@media screen and (min-width: 1020px)  {                   
body {
        background-color: red;
        background-image: url('./cubes.png');
}
.Box {
        margin: 10% 0% 0% 60%;
}

h1 {
        font-size: 15px;
        padding: 20px;
        color: Black;
}

input {
        border: 0px none;
        border-bottom: solid Black;
        background: transparent;
        padding: 10px;
        width: 50%;
        font-size: 25px;
}

li {
        font-size: 25px;
}

button {
        padding: 10px;
        margin-left: 10px;
        border-radius: 4px;
        background-color: white;
        border: 2px solid black;
}

button:hover {
        background-color: black;
       
        color: white;
}
.Header{
        display:flex;
        background-color: silver;
        width: 100%;

}
.time{
        position: right;
        padding-left: 65% !important;
        
}

.Footer {
        text-align: center;
        position: fixed;
        left: 0;
        bottom: 0;
        background-color: silver;
        width: 100%;
        color: white;
}
}
/* *-------------------------------------------------------------Tab-------------------------------------------------*/
@media screen and (max-width: 720px) {
  body {
        background-color: yellow;
         background-image: url('./cubes.png');
  }
  .Box {
        margin: 30% 0% 0% 30%;
}

h1 {
        font-size: 20px;
        padding: 1px;
        color: Black;
}
 }

/**-------------------------------------------------------phone---------------------------------------------------**/
